import React, { useEffect } from "react";
import 'aos/dist/aos.css'; 
import AOS from 'aos';
import "./styles/footer.css";
import FooterSet from "./FooterSet";
import FooterData from "./FooterData";

function Footer() {
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  }, []);

  return (
    <section id="footer">
      <div className="footer vertical-stack" data-aos="fade-up" data-aos-duration="1000">
        <p data-aos="fade-up" data-aos-duration="1200">Thanks for Swinging By - Let's connect!</p>
        <p data-aos="fade-up" data-aos-duration="1400">~ Coded with ❤️ by Anahat ~</p>
        <div className="footers-container horizontal-stack" data-aos="fade-up" data-aos-duration="1600">
          {FooterData.map((element) => (
            <FooterSet
              key={element.key}
              type={element.type}
              title={element.title}
              url={element.url}
            />
          ))}
        </div>
      </div>
    </section>
  );
}

export default Footer;